/**
 * core
 */
.toolbar,
.toolbar-button,
.styler,
.styler-button {
  display: flex;
  justify-content: center;
  align-items: center;
}
@font-face {
  font-weight: 700;
  font-family: "Poppins";
  src: url("../font/Poppins-Bold.ttf");
}
@font-face {
  font-weight: 600;
  font-family: "Poppins";
  src: url("../font/Poppins-SemiBold.ttf");
}
@font-face {
  font-weight: 500;
  font-family: "Poppins";
  src: url("../font/Poppins-Medium.ttf");
}
@font-face {
  font-weight: 400;
  font-family: "Poppins";
  src: url("../font/Poppins-Regular.ttf");
}
@font-face {
  font-weight: 300;
  font-family: "Poppins";
  src: url("../font/Poppins-Light.ttf");
}
.icon {
  width: 20px;
  height: 20px;
}
/**
 * eidtor elements
 */
.editor {
  max-height: calc(100vh - 140px);
  margin: 70px 100px;
  overflow: auto;
}
.editor:focus {
  outline: none;
}
.editor-image {
  display: block;
  margin: auto;
  width: 100%;
  max-width: 400px;
}
.toolbar {
  background-color: #323c47;
  padding: 5px;
  margin: 0;
  width: auto;
  list-style: none;
  border-radius: 20px;
}
.toolbar li:not(last-child) {
  padding-right: 5px;
}
.toolbar-button {
  position: relative;
  width: 30px;
  height: 30px;
  padding: 0;
  margin: 0;
  border-radius: 15px;
  border: 0;
  outline: none;
  background-color: #fff;
  fill: #323c47;
}
.toolbar-list {
  -webkit-appearance: none;
  padding: 5px 10px;
  height: 30px;
  border-radius: 15px;
  background-color: #fff;
  font-size: 15px;
  line-height: 15px;
}
.toolbar-input {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.styler {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  background-color: #fff;
  padding: 20px 0;
  margin: 0;
  width: auto;
  list-style: none;
}
.styler li:not(last-child) {
  margin-right: 1px;
}
.styler-separator {
  content: '|';
  padding: 0 10px;
}
.styler-button {
  position: relative;
  width: 30px;
  height: 30px;
  padding: 0;
  margin: 0;
  border-radius: 15px;
  border: 0;
  outline: none;
  background-color: #fff;
  fill: #323c47;
}
.styler-button.is-active {
  fill: #4da1ff;
}
.styler-list {
  -webkit-appearance: none;
  padding: 5px 10px;
  height: 30px;
  border-radius: 15px;
  background-color: #fff;
  font-size: 15px;
  line-height: 15px;
}
.styler-input {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.styler-color {
  height: 30px;
  width: 30px;
  border: 0;
  padding: 0;
  box-sizing: border-box;
}
.styler-select {
  appearance: none;
  height: 30px;
  border-radius: 0;
  padding: 2px 15px;
  line-height: 26px;
  background: #fff;
  text-align: center;
  border-width: 1px;
  border-color: #323c47;
}
.styler.is-visiable {
  display: flex;
}
